.home {
    padding: 48px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    gap: 96px;
}

.home-hero {
    display: flex;
    flex-direction: column;
    gap: 96px;

    padding-top: 48px;
}

.home-hero-one-title {
    font-size: 128px;
    line-height: 1;

    margin-left: -8px;
}

.home-hero-one-subtitle {
    font-size: 16px;
    color: #afafaf;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.home-hero-two {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 48px;
    justify-content: space-between;
}

.home-hero-two-one {
    width: 496px;
}

.home-hero-two-one-paragraph {
    font-size: 16px;
}

.home-hero-two-one-paragraph a {
    color: black;
    text-decoration: underline;
}

.home-hero-two-two {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.home-hero-two-two-span {
    width: 248px;
    height: 48px;

    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    border-radius: 8px;

    cursor: pointer;

    -webkit-transition: background-color 0.2s ease-in-out, border 0.2s ease-in-out;;
    transition: background-color 0.2s ease-in-out, border 0.2s ease-in-out;
}

.home-hero-two-two-span.primary {
    background-color: #e8e8e8;
}

.home-hero-two-two-span.primary:hover {
    background-color: #eeeeee;
}

.home-hero-two-two-span.secondary {
    border: 2px solid #e8e8e8;
}

.home-hero-two-two-span.secondary:hover {
    border: 2px solid #d8d8d8;
}

.home-projects {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 48px;
}

.home-projects-list {
    list-style-type: none;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 48px 24px;

}

.home-projects-list-item {
    background: #e4e4e4;
    width: 300px;
    flex-grow: 1;
    border-radius: 8px;
    overflow: hidden;
}

.home-projects-list-item-image {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    cursor: pointer;

    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;

    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    -o-transform: scale(1.05);
    transform: scale(1.05);
}

.home-projects-list-item-image:hover {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
    filter: saturate(125%);
}

.home-projects-list-item p {
    position: absolute;
}

.home-contact {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 24px;
    padding-top: 48px;
}

.home-contact-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    flex-grow: 1;
}

.home-contact-title {
    font-size: 64px;
    position: relative;
}

.home-contact-form-field {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.home-contact-form-field-label {
    font-size: 12px;
}

.home-contact-form-field-input {
    width: 100%;

    box-sizing: border-box;
    padding: 8px;

    font-family: 'Inter', Arial, sans-serif;
    font-size: 14px;

    border-radius: 4px;
    border: 1px solid #afafaf;

    background: transparent;

    -webkit-transition: border 0.2s ease-in-out;
    transition: border 0.2s ease-in-out;
}

.home-contact-form-field-input:focus {
    outline: none;
    border: 1px solid #000000;
}

.home-contact-form-field-input::placeholder {
    font-family: 'Inter', Arial, sans-serif;
    font-size: 14px;
    color: #8d8d8d;
}

textarea {
    resize: none;
    flex-grow: 1;
    height: 148px;
}

form button {
    cursor: pointer;
    margin: 8px 0 0 0;
    padding: 16px !important;
}

form button:hover {
    border: 1px solid #000000;
}

.home-footer {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.home-footer-one-list {
    list-style-type: none;
}

.home-footer-one-list li {
    font-size: 14px;
}

.home-footer-one-list li span {
    text-decoration: underline;
}

.home-footer-two {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media screen and (max-width: 887px) {
    .home-hero-one-title {
        font-size: 86px;
        margin-left: -4px;
    }
    .home-hero-one-subtitle {
        font-size: 14px;
    }
}

@media screen and (max-width: 794px) {
    .home-projects-title,
    .home-contact-title {
        position: relative;
        top: 0;
    }
}

@media screen and (max-width: 692px) {
    .home-hero-one-title {
        font-size: 64px;
        margin-left: -4px;
    }
    .home-hero-one-subtitle {
        font-size: 13px;
    }
    .home-hero-two-one {
        width: 414px;
    }
}

@media screen and (max-width: 516px) {
    .home,
    .home-hero {
        gap: 72px;
    }
    .home {
        padding: 24px;
    }
    .home-hero-two-one {
        width: 348px;
    }
    .home-projects-title {
        font-size: 48px;
    }
    .home-contact {
        gap: 36px;
    }
    .home-contact-title {
        font-size: 48px;
    }
}

@media screen and (max-width: 396px) {
    #root {
        width: 396px;
    }
}